div#headerContainer {
  direction: rtl;
}
img#logo {
  border-style: solid;
  border-width: 0px;
  border-color: black;
  float: right;
  width: 90px;
  height: 90px;
  margin-top: 5px;
  margin-right: 15px;
}

div#NavBarContainer {
  font-family: Arial, Helvetica, sans-serif;
  padding-top: 40px;
  background-color: #333;
  padding-right: 0px;
}

div#bodyContainer {
  width: 60%;
  min-height: 400px;
  border-style: solid;
  border-width: 3px;
  border-color: #9876;
  margin-left: 20%;
  align-content: center;
  justify-content: table;

  display: flex;
  align-items: center;
  direction: rtl;
  /* background-image: url("./imgs/logo.jpeg");
  opacity: 0.05; */
  background-color: #ffffff; /* Used if the image is unavailable */
  /* height: 500px; You must set a specified height */
  background-position: center; /* Center the image */
  background-repeat: no-repeat; /* Do not repeat the image */
  background-size: cover;
}
